.container {
  position: relative;
  width: 100%;
  height: 100vh;
  background: url("/login-bg.jpg") no-repeat center center/cover;
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay {
  position: absolute;
  inset: 0;
  background: rgba(10, 20, 40, 0.6);
  backdrop-filter: blur(6px);
}

.loginBox {
  position: relative;
  z-index: 10;
  width: 400px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(12px);
  border-radius: 16px;
  padding: 40px 30px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  color: #fff;
  text-align: center;
}

.title {
  font-size: 26px;
  font-weight: 700;
  margin-bottom: 8px;
  background: linear-gradient(to right, #3498db, #9b59b6);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.subtitle {
  font-size: 14px;
  color: #ddd;
  margin-bottom: 20px;
}

.error {
  color: #ff6b6b;
  margin-bottom: 12px;
  font-size: 14px;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.inputGroup {
  display: flex;
  align-items: center;
  background: rgba(255, 255, 255, 0.15);
  padding: 12px 14px;
  border-radius: 10px;
}

.inputGroup input {
  flex: 1;
  border: none;
  outline: none;
  background: transparent;
  color: #fff;
  margin-left: 10px;
  font-size: 15px;
}

.icon {
  font-size: 16px;
  color: #ccc;
}

.captchaRow {
  display: flex;
  align-items: center;
  gap: 10px;
}

.captchaBox {
  flex: 1;
  background: rgba(255, 255, 255, 0.2);
  font-weight: bold;
  font-size: 18px;
  text-align: center;
  border-radius: 8px;
  padding: 10px;
  letter-spacing: 3px;
}

.captchaInput {
  flex: 2;
  padding: 12px;
  border-radius: 8px;
  border: none;
  outline: none;
  font-size: 15px;
}

.refreshBtn {
  border: none;
  background: #3498db;
  color: white;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s;
}

.refreshBtn:hover {
  background: #2980b9;
}

.button {
  background: linear-gradient(135deg, #3498db, #9b59b6);
  border: none;
  padding: 14px;
  border-radius: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  margin-top: 12px;
  transition: transform 0.2s ease, background 0.3s ease;
}

.button:hover {
  transform: scale(1.03);
  background: linear-gradient(135deg, #2980b9, #8e44ad);
}

.footerLinks {
  margin-top: 18px;
  display: flex;
  justify-content: space-between;
  font-size: 13px;
}

.footerLinks a {
  color: #ccc;
  text-decoration: none;
  transition: color 0.2s;
}

.footerLinks a:hover {
  color: #fff;
}
/* üëÅÔ∏è Password Wrapper */
.passwordWrapper {
  position: relative;
  flex: 1;
  display: flex;
  align-items: center;
}

.passwordWrapper input {
  width: 100%;
  padding-right: 40px; /* Space for eye icon */
}

/* üëÅÔ∏è Eye Button */
.eyeBtn {
  position: absolute;
  right: 10px;
  background: none;
  border: none;
  color: #ccc;
  font-size: 18px;
  cursor: pointer;
  transition: color 0.3s ease;
}

.eyeBtn:hover {
  color: #fff;
}
